body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgp((234,216,228));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

div.section {
  /*padding-top:100px;*/
  min-height: 100vh;
  min-width: 100vh;
  padding: 80px;
}
h1, h2, p, ul, li {
  font-family: sans-serif;
}

ul.header li {
  display: inline;
  list-style-type: none;
  margin: 0;
}

ul.header {
  background-color: gainsboro;
  padding: 0;
}

ul.header li a {
  color: cornflowerblue;
  font-weight: bold;
  text-decoration: none;
  padding: 20px;
  display: inline-block;
}

ul.header li a:hover {
  color: navy;
}

.active { 
  color: aliceblue !important;
  background-color: cornflowerblue;
 
}

.content {
  background-color: azure;
  padding: 20px;
}

.content h2 {
  padding: 0;
  margin: 0;
}

.content li {
  margin-bottom: 10px;
}

.home {
  background-color: rgb(234,216,228);
}

.slide {
  background-color: rgb(248,228,221);
}

.product {
  background-color: #78c0a8;
}
.App {
  text-align: center;
}

.App-logo {
  margin-top: 50px;
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 10vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.bg-dark {
    background-color: #492711!important;
}

/*social icon*/
.my-social-icon .social-svg {
    color: wheat;
}

.my-social-icon .social-svg:hover {
    color: #55acee;
    background: white;
}

g.social-svg-mask {
    fill: rgb(200, 200, 200) !important;
}

g.social-svg-mask:hover {
    fill: rgb(230, 230, 230) !important;
}
/*social icon*/

/*nav*/
a.nav-link {
    font-weight: bold;
    cursor: pointer;
    color: rgb(190,184,172) !important;
}
